*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  object-fit: cover;
}

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.is-frozen {
  overflow: hidden;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #0E151F;
  color: #F9F9F9;
  overflow-x: hidden;
}

h2 {
  font-size: 2rem;
  line-height: 1.5;
  font-weight: 700;
  position: relative;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;
}
h2::before {
  content: "";
  background-color: rgba(241, 194, 27, 0.8);
  height: 1.5px;
  width: 100%;
  position: absolute;
  bottom: -5px;
  left: 0;
}
@media (min-width: 768px) {
  h2 {
    width: fit-content;
    text-align: left;
  }
  h2::before {
    bottom: 0px;
    height: 2px;
  }
}

.sprite {
  display: inline-block;
  background-image: url("../images/sprite.png");
  background-size: 1862px 1454px;
  background-repeat: no-repeat;
}

