  background-position: -10px -1091px;
}

.locate-icon {
  width: 152px;
  height: 188px;
  background-position: -1657px -217px;
}

.wave {
  width: 1440px;
  height: 360px;
  background-position: -10px -10px;
  max-width: 100vw;
}

.logo {
  background-position: -1657px -151px;
  height: 45px;
  width: 195px;
}
@media (min-width: 768px) {
  .logo {
    width: 195px;
    height: 187px;
    background-position: -1657px -10px;
  }
}

.consultant {
  width: 167px;
  height: 200px;
  background-position: -1470px -10px;
}

.vasthu {
  width: 265px;
  height: 264px;
  background-position: -696px -776px;
}

.ppa {
  width: 305px;
  height: 217px;
  background-position: -981px -776px;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .main-box {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    position: relative;
  }
}
main {
  margin-top: 64px;
}
@media (min-width: 768px) {
  main {
    margin-top: 0;
    width: 70%;
    max-width: 1090px;
    margin-left: min(30%, 350px);
  }
}

.wave {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: #F6D400;
  height: 64px;
  width: 100%;
  padding: 0 20px 0 10px;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  header {
    background: linear-gradient(to bottom, #F6D400 0%, #F6D400 30%, rgba(241, 194, 27, 0.8) 100%);
    width: 30vw;
    max-width: 350px;
    height: 100vh;
    flex-direction: column;
    padding: 30px 20px;
    position: fixed;
    top: 0;
    left: 0;
  }
}

@media (max-width: 767px) {
  nav {
    position: absolute;
    height: 0;
    max-height: 100vh;
    top: 64px;
    left: 0;
    width: 100vw;
    background-image: linear-gradient(to top, #0E151F, #161616);
    box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
    transition: all 0.4s ease;
    overflow: hidden;
  }
}
nav.is-open {
  height: calc(100vh - 64px);
  display: block;
}

.nav-list {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.nav-list a {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.125rem;
}
.nav-list a.is-active {
  color: #F6D400;
}
@media (min-width: 768px) {
  .nav-list a {
    color: #036765;
  }
  .nav-list a.is-active {
    color: #161616;
    font-weight: 700;
  }
}

@media (max-width: 767px) {
  .cta-sidepane {
    display: none;
  }
}
.cta-sidepane .cta-text {
  color: #161616;
  font-weight: 600;
}
.cta-sidepane .cta-btn {
  background: #036765;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hamburger {
  height: 30px;
  width: 35px;
  position: relative;
  overflow: hidden;
}
.hamburger::before, .hamburger::after {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  height: 4px;
  width: 100%;
  background: #161616;
  transition: transform 0.2s linear;
}
.hamburger::before {
  top: 0;
  box-shadow: 10px 14px 0 0 #161616;
}
.hamburger::after {
  bottom: 0;
}
.hamburger.is-active::before {
  box-shadow: none;
  transform: rotate(45deg) translate(10px, 10px);
}
.hamburger.is-active::after {
  transform: rotate(-45deg) translate(8px, -9px);
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.banner {
  padding: 225px 0 50px;
  text-align: center;
}
@media (min-width: 768px) {
  .banner {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 100px 0;
    height: clamp(400px, 100vh, 700px);
  }
}

h1 {
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 1.5;
}
h1 span {
  color: #F6D400;
}
@media (min-width: 768px) {
  h1 {
    font-size: 2.25rem;
  }
}
@media (min-width: 1024px) {
  h1 {
    font-size: 2.75rem;
  }
}

.banner-caption {
  color: rgba(200, 200, 200, 0.6);
  font-size: 1.25em;
  line-height: 1.6;
  font-weight: 300;
  margin-top: 30px;
}
@media (min-width: 1024px) {
  .banner-caption {
    font-size: 1.675rem;
  }
}

.cta-banner {
  margin-top: 20px;
}
@media (min-width: 768px) {
  .cta-banner {
    display: none;
  }
}

.cta-text {
  color: #C8C8C8;
  font-size: 1rem;
  font-weight: 400;
}

.cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 15px 30px;
  margin: 12px auto 0;
  width: fit-content;
  font-weight: 600;
  font-size: 1.125rem;
  border-radius: 24px;
  background: linear-gradient(to right, #F6D400 0%, #F6D400 20%, rgba(241, 194, 27, 0.8) 100%);
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
}
.cta-btn:hover, .cta-btn:focus, .cta-btn:active {
  background-color: rgba(241, 194, 27, 0.8);
}

.phone-icon {
  fill: #F9F9F9;
  height: 20px;
  width: 20px;
}

.blob-home {
  display: none;
}
@media (min-width: 1024px) {
  .blob-home {
    display: block;
    position: absolute;
    top: -100px;
    right: -240px;
    z-index: -2;
  }
}

.services {
  padding-top: 80px;
}

.service-box {
  padding: 50px 20px;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
  border-radius: 0;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
  position: relative;
  z-index: 2;
}
.service-box:not(:last-child) {
  margin-bottom: 30px;
}
.service-box::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.service-box > * {
  position: relative;
  z-index: 3;
}
@media (min-width: 1024px) {
  .service-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 30px;
    gap: 25px;
  }
}

