  background-image: url("../images/vasthu-bg.png");
}
.vasthu-box .sb-image {
  order: -1;
}

.ppa-box {
  background-image: url("../images/ppa-bg.png");
}
.ppa-box .sb-image {
  order: -1;
}

.consultant-box {
