.projects {
  padding-top: 80px;
  position: relative;
}

.projects-inner {
  width: 100%;
  border-radius: 0;
  background-color: #1A2029;
  padding: 10px;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
  overflow: hidden;
}
@media (min-width: 1024px) {
  .projects-inner {
    gap: 25px;
    padding: 20px;
    columns: 2;
  }
}

.projects-figure {
  width: 100%;
  position: relative;
}
.projects-figure:not(:last-child) {
  margin-bottom: 25px;
}
.projects-figure img {
  border-radius: 0;
  width: 100%;
  height: auto;
}
.projects-figure figcaption {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 10px;
  background-color: rgba(26, 32, 41, 0.75);
  color: #F9F9F9;
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.5;
  text-align: center;
}

.btn-project {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  z-index: 2;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.9);
  display: none;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.is-shown {
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-contents {
  width: 90%;
  max-width: 768px;
  border-radius: 5px;
  background-color: #1A2029;
  padding: 50px 0 25px;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
  position: absolute;
  overflow: hidden;
  position: relative;
  transform: scale(0);
  transition: transform 0.5s ease;
}
.is-shown .modal-contents {
  transform: scale(1);
}

.close-btn {
  height: 25px;
  width: 25px;
  position: absolute;
  top: 10px;
  right: 15px;
}
.close-btn::before, .close-btn::after {
  content: "";
  height: 100%;
  width: 2.5px;
  background-color: currentColor;
  position: absolute;
  top: 0;
  left: 10px;
}
.close-btn::before {
  transform: rotate(45deg);
}
.close-btn::after {
  transform: rotate(-45deg);
}
.close-btn:hover, .close-btn:focus, .close-btn:active {
  opacity: 0.5;
}

.modal-image {
  width: 100%;
  margin-bottom: 10px;
}

.modal-caption {
  font-size: 1rem;
  line-height: 1.6;
  font-weight: 400;
  color: #F9F9F9;
  padding: 0 15px;
  text-align: center;
}

.blob-projects {
