  background-position: center center;
  border-radius: 0;
  overflow: hidden;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.75);
}
@media (min-width: 600px) {
  .contact-inner {
    padding: 50px 40px;
  }
}

.contact-row {
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.contact-row:not(:last-child) {
  margin-bottom: 20px;
}
.contact-row svg {
  position: relative;
  top: 3px;
}

address {
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.8;
  color: #F9F9F9;
  margin-left: auto;
  width: calc(100% - 46px);
  max-width: 300px;
}

address a {
  display: block;
}
address a:hover, address a:focus, address a:active {
  color: #F6D400;
  text-decoration: underline;
}

.blob-contact {
  display: none;
}
@media (min-width: 1024px) {
  .blob-contact {
    position: absolute;
    z-index: -1;
    display: block;
    top: 0;
    left: -25px;
  }
}

footer {
  padding: 25px 0 15px;
  background-color: #1A2029;
}
@media (min-width: 768px) {
  footer {
    padding: 20px 0;
    margin-left: min(30%, 350px);
  }
  footer .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.social-links a {
  height: 30px;
  width: 30px;
}
.social-links a:hover svg, .social-links a:focus svg, .social-links a:active svg {
  fill: #F9F9F9;
}
.social-links svg {
  fill: #C8C8C8;
  height: 30px;
  width: 30px;
}

.text-copyrights {
  color: #C8C8C8;
  text-align: center;
  line-height: 2;
  display: block;
  font-weight: 400;
}
@media (max-width: 768px) {
  .text-copyrights {
    margin: 30px auto 0;
  }
}
@media (max-width: 768px) {
  .text-copyrights span {
    display: block;
  }
}